@import "../../utils/variables.scss";

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(3px);
  z-index: 12;
  padding: 80px 40px 40px;

  .content {
    background-color: #ffffff;
    border-radius: 20px;

    .modalController {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px 20px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.12);
      margin-bottom: 20px;
      overflow-x: hidden;
      overflow-y: scroll;

      p {
        color: $themePurple;
        font-size: 20px;
        font-weight: 700;
      }

      button {
        border: none;
        outline: none;
        cursor: pointer;
        background-color: #ffffff;

        svg {
          stroke: #1e1e1e;
          stroke-width: 3px;
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .backdrop {
    padding: 10px;
    .content {
      margin-top: 40%;
      min-width: 90%;
      max-width: 95%;
    }
  }
}